import React, { Component } from 'react';
import { Donut } from 'britecharts-react';

export class DoughnutChartDemo extends Component {
    donutDataWith4Slices = [
        {
            quantity: 60,
            percentage: 60,
            name: 'React',
            id: 1,
        },
        {
            quantity: 20,
            percentage: 20,
            name: 'Ember',
            id: 2,
        },
        {
            quantity: 10,
            percentage: 10,
            name: 'Angular',
            id: 3,
        },
        {
            quantity: 10,
            percentage: 10,
            name: 'Backbone',
            id: 4,
        },
    ];
    marginObject = {
        left: 100,
        right: 40,
        top: 40,
        bottom: 40,
    };


    render() {

        return (
            <Donut
                data={this.donutDataWith4Slices}
                width={200}
                height={200}
                margin={this.marginObject}
                externalRadius={250 / 2.5}
                internalRadius={250 / 5}
            />
        )
    }
}