import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        '& > *': {
            margin: theme.spacing(1),
        },
    },
}));

export default function People() {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <Avatar alt="Remy Sharp" src="https://www.gstatic.com/tv/thumb/persons/983712/983712_v9_ba.jpg" />
            <Avatar alt="Travis Howard" src="https://www.gstatic.com/tv/thumb/persons/673351/673351_v9_ba.jpg" />
            <Avatar alt="Cindy Baker" src="https://upload.wikimedia.org/wikipedia/commons/thumb/6/6d/Luis_Su%C3%A1rez_2018.jpg/220px-Luis_Su%C3%A1rez_2018.jpg" />
            <Avatar alt="Cindy Baker" src="https://i.ytimg.com/vi/jffVm6tNBDg/maxresdefault.jpg" />
            <Avatar alt="Cindy Baker" src="https://young-sheldon.com/assets/images/cast/MontanaJordan.jpg" />
            <Avatar alt="Cindy Baker" src="https://i.ytimg.com/vi/jffVm6tNBDg/maxresdefault.jpg" />
        </div>
    );
}
