import React, { Component } from 'react'
import './PriceBanner.css'
export default class PriceBanner extends Component {
    render() {
        return (
            <div>
                <div className="price-banner-parent">
                    <div className="title-price-text-navbar">Account Balance</div>
                    <div className="price-text-navbar">$332.15</div>
                </div>
                <div className="flex-box">
                    <div className="split-box">
                        <div className="title-price-text-credit-navbar">Income</div>
                        <div className="price-text-credit-navbar">$632.95</div>
                    </div>
                    <div className="split-box">
                        <div className="title-price-text-credit-navbar">Expenses</div>
                        <div className="price-text-credit-navbar debit">$300.85</div>
                    </div>
                </div>
            </div>
        )
    }
}
